import { toast, Slide } from "react-toastify";
import type { ToastOptions } from "react-toastify";
import styled, { css } from "styled-components";
import { IconCorrect, IconIncorrect } from "../icons";
import { paasNeutral } from "../styles/colors";

export const commonOption: Partial<ToastOptions> = {
  autoClose: 1500,
  closeButton: false,
  theme: "dark",
  style: {
    borderRadius: 8,
    padding: "16px 14px 16px 30px",
    backgroundColor: paasNeutral.neutral_5,
  },
  transition: Slide,
};

function ToastComponent({
  message,
  icon,
}: {
  message: string;
  icon: React.ReactNode;
}) {
  return (
    <Wrapper>
      {icon}
      <span>{message}</span>
    </Wrapper>
  );
}

export function SuccessToast({ message }: { message: string }) {
  return <ToastComponent message={message} icon={<IconSuccess />} />;
}
export function ErrorToast({ message }: { message: string }) {
  return <ToastComponent message={message} icon={<IconError />} />;
}

/**
 * @description To fix: when called by parent project, maybe different toast instance cause some issues
 * @issues https://github.com/fkhadra/react-toastify/issues/931 maybe relative
 */
export function success(message: string, options: ToastOptions = {}) {
  return toast(<SuccessToast message={message} />, {
    ...commonOption,
    ...options,
  });
}

export function error(message: string, options: ToastOptions = {}) {
  return toast(<ErrorToast message={message} />, {
    ...commonOption,
    ...options,
  });
}

/** @description waiting for ui: error, info, ... */
const Toast = {
  /** @deprecated to fix the usage in parent project */
  success,
  /** @deprecated to fix the usage in parent project */
  error,
  commonOption,
  SuccessToast,
  ErrorToast,
} as const;

export default Toast;

const Wrapper = styled.div`
  ${(p) => p.theme.flexCSS.row}
  ${(p) => p.theme.fontStyleDict.caption}
  font-size: 12px;
  align-items: center;
  color: #fff;
`;

const commonIconCSS = css`
  width: 20px;
  height: 20px;
  margin-right: 24px;
  flex: none;
`;

const IconSuccess = styled(IconCorrect)`
  ${commonIconCSS}
  color: ${(p) => p.theme.colors.correct};
  fill: #fff;
`;
const IconError = styled(IconIncorrect)`
  ${commonIconCSS}
  color: ${(p) => p.theme.colors.error};
  fill: #fff;
`;
